import { analyzeAndValidateNgModules, identifierModuleUrl } from '@angular/compiler';
import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RestApiSQL } from '../service/rest.service';


@Component({
  selector: 'app-patient-crud',
  templateUrl: './patient-crud.component.html',
  styleUrls: ['./patient-crud.component.css']
})

export class PatientCRUDComponent implements OnInit {
  public form!:FormGroup;
  public Ptes:any = [];
  public nombres:Array<any> = []
  NewPte:any={
        fullName:"",
        IdPte:"",
        phone:"",
        email:""
  };
  @Output() PteSelected = new EventEmitter();

  constructor(private RestService: RestApiSQL, private formBuilder: FormBuilder) { }

  ngOnInit(): void {
    this.form=this.formBuilder.group({
      fullName: [''],
      IdPte: [''],
      phone: [''],
      email: ['']
    })
    this.actualizarPtes();           
  }
  public actCrearPte(){
    var Pteactual: string = this.form.value.fullName;
    var Existe: boolean = false;

    this.Ptes.forEach((pte:any) => {
      if (pte['fullName'] === Pteactual) {
        Existe = true;        
      }
    });

    if (Existe) {
      console.log("el paciente existe desea actualizarlo?")
    } else {
      this.Ptes.push({
        idpacientes: 0,
        fullName: this.form.value.fullName,
        IdPte: this.form.value.IdPte,
        phone: this.form.value.phone,
        email: this.form.value.email,
      })
      console.log(
        this.form.value.fullName,
        this.form.value.IdPte,
        this.form.value.phone,
        this.form.value.email,
        this.Ptes)
        if (this.form.value.fullName === null) {
          console.log("esto esta nulo")
        } else {
          this.RestService.post('http://18.218.109.116/pacientes',  
          {
            fullName:this.form.value.fullName,
            IdPte:this.form.value.IdPte,
            phone:this.form.value.phone,
            email:this.form.value.email
          }
          )
          .subscribe(respuesta => {
            alert("Paciente Guardado con exito");
          });
        }
    }
  }

  public leerPte(){
    this.form.reset();
  }

  public CargaDatos(e: any){
    var nameSel:string = e.target.value
    const idtxt:any = document.querySelector('#id');
    const phonetxt:any = document.querySelector("#phone");
    const emailtxt:any = document.querySelector("#email");
    
    this.Ptes.forEach((pte: any) => {
      if (nameSel === pte['fullName']) {
        idtxt.value = pte['IdPte'];
        phonetxt.value = pte['phone'];
        emailtxt.value = pte['email'];
        this.PteSelected.emit (pte); 
        this.NewPte['IdPte']=pte['IdPte'];
        this.NewPte['phone']=pte['phone'];
        this.NewPte['email']=pte['email'];
      } else {
        this.PteSelected.emit(this.NewPte);
      }
    });
  }

  public GenRepo(){
    print();
  }

  actualizarPtes(){
    this.RestService.get('http://18.218.109.116/pacientes')
    .subscribe(res =>{
      this.Ptes = (res);
      this.Ptes.forEach((pte: { [x: string]: any; }) => {
        this.nombres.push(pte['fullName']);
      });
    })
  }

}

